import React from "react";
import Link from "gatsby-link";
import OwlCarousel from 'react-owl-carousel';
import "../sass/Bodymap.sass";

class BodyMap extends React.Component {
	render() {
		return (
			<div className="bodyMap">
			<h3 className="bodyMap-title bodyMap-title-top">Группы мышц</h3>
				<div className="bodyMap-tags">
					<OwlCarousel autoplay dots={false} autoplaySpeed={500} loop items={4}>
						<Link to={`/bodypart/shoulders`} className="bodyMap-tag" style={shouldersStyle}>Плечи</Link>
						<Link to={`/bodypart/chest`} className="bodyMap-tag" style={chestStyle}>Грудь</Link>
						<Link to={`/bodypart/biceps`} className="bodyMap-tag" style={bicepsStyle}>Бицепс</Link>
						<Link to={`/bodypart/triceps`} className="bodyMap-tag" style={tricepsStyle}>Трицепс</Link>
						<Link to={`/bodypart/back`} className="bodyMap-tag" style={backStyle}>Спина</Link>
						<Link to={`/bodypart/abs`} className="bodyMap-tag" style={absStyle}>Пресс</Link>
						<Link to={`/bodypart/legs`} className="bodyMap-tag" style={legsStyle}>Ноги</Link>
						<Link to={`/bodypart/cardio`} className="bodyMap-tag" style={cardioStyle}>Кардио</Link>
					</OwlCarousel>
				</div>
				<h3 className="bodyMap-title">Инвентарь</h3>
				<div className="bodyMap-tags">
					<OwlCarousel autoplay dots={false} autoplaySpeed={900} autoplayTimeout={6000} loop items={3}>
						<Link to={`/inventory/dumbbells`} className="bodyMap-tag" style={dumbellsStyle}>Гантели</Link>
						<Link to={`/inventory/stange`} className="bodyMap-tag" style={stangeStyle}>Штанга</Link>
						<Link to={`/inventory/smit`} className="bodyMap-tag" style={smitStyle}>Тренажер Смита</Link>
						<Link to={`/inventory/trx`} className="bodyMap-tag" style={trxStyle}>TRX петли</Link>
						<Link to={`/inventory/scott`} className="bodyMap-tag" style={scottStyle}>Скамья Скотта</Link>
						<Link to={`/inventory/ezstange`} className="bodyMap-tag" style={ezStyle}>EZ-гриф</Link>
						<Link to="/inventory/ownweight" className="bodyMap-tag" style={OwnWeightStyle}>Собственный вес</Link>
					</OwlCarousel>
				</div>
				<h3 className="bodyMap-title">Тип упражнения</h3>
				<div className="bodyMap-tags">
					<OwlCarousel dots={false} items={2}>
						<Link to={`/type/base`} className="bodyMap-tag bodyMap-tag-type" style={baseStyle}>Базовое</Link>
						<Link to={`/type/isolated`} className="bodyMap-tag bodyMap-tag-type" style={isolatedStyle}>Изолирующее</Link>
					</OwlCarousel>
				</div>
				<Link to="/bmi" className="bodyMap-btn-bmi">BMI Калькулятор</Link>
			</div>
		);
	}
}

const shouldersStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/ji0zvnl67/shoulders.jpg)',
	backgroundPosition: "bottom"
};

const chestStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/uudldf6pr/chest.jpg)',
	backgroundPosition: "center"
};

const bicepsStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/tfc0ooxwv/biceps.jpg)',
	backgroundPosition: "center"
};

const tricepsStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/5pmkzqzgv/triceps.jpg)',
	backgroundPosition: "center"
};

const backStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/azrjra4cv/back.jpg)',
	backgroundPosition: "center"
};

const absStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/otfwgbp8f/abs.jpg)',
	backgroundPosition: "center"
};

const legsStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/t2kmij2sf/legs.jpg)',
	backgroundPosition: "center"
};

const cardioStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/v74zjlwpb/cardio.jpg)',
	backgroundPosition: "bottom"
};

const dumbellsStyle = {
	backgroundImage: 'url(https://www.bodyboss.ru/upload/resize_cache/iblock/ca5/1110_624_2/ca5aa884565523cdcc9006440dc32b45.jpg)',
	backgroundPosition: "center"
};

const scottStyle = {
	backgroundImage: 'url(http://www.fitnesstechno.com/i/catalog/150/713458/713458_pthrf_800_600.jpg?v=147681)',
	backgroundPosition: "center"
};

const smitStyle = {
	backgroundImage: 'url(https://bodysolid-russia.ru/ckfinder/userfiles/images/smith_machine_002.jpg)',
	backgroundPosition: "center"
};

const ezStyle = {
	backgroundImage: 'url(http://www.fitness-industry.com.ua/photo/106r.jpg)',
	backgroundPosition: "center"
};

const stangeStyle = {
	backgroundImage: 'url(https://img1.goodfon.ru/wallpaper/big/b/a0/gym-fitness-shtanga-1.jpg)',
	backgroundPosition: "center"
};

const trxStyle = {
	backgroundImage: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTYsmWhDs5hp40dUNcy3WwRsaqE0IB9czvdWanb2WOmmgJq2PYKZg)',
	backgroundPosition: "center"
};

const OwnWeightStyle = {
	backgroundImage: 'url(https://s33.postimg.cc/vf2pagk33/image.jpg)',
	backgroundPosition: "center"
};

const baseStyle = {
	background: '#222'
};

const isolatedStyle = {
	background: '#222'
};

export default BodyMap;



