import React from "react"
import Link from "gatsby-link"
import Menu from 'react-burger-menu/lib/menus/slide'
// import ThemeSwitch from './ThemeSwitch'
import '../sass/SideMenu.sass'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faAngleRight from '@fortawesome/fontawesome-free-solid/faAngleRight'


class SideMenu extends React.Component {
  render () {
    return (
        <Menu isOpen={ false }>
          <h6>Меню</h6>
          {/* <ThemeSwitch /> */}
          <div className="menu-category">
          <h3 className="menu-category-name">Основное</h3>
            <div className="menu-category-items-h">
              <Link to="/" className="menu-top-item">Главная</Link>
              <Link to="/bmi" className="menu-top-item">BMI</Link>
            </div>
          </div>
          <div className="menu-category">
            <h3 className="menu-category-name">Группы мышц</h3>
            <div className="menu-category-items">
              <Link to="/bodypart/shoulders" className="menu-category-item">Плечи <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/chest" className="menu-category-item">Грудь <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/biceps" className="menu-category-item">Бицепс <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/triceps" className="menu-category-item">Трицепс <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/back" className="menu-category-item">Спина <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/legs" className="menu-category-item">Ноги <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/cardio" className="menu-category-item">Кардио <FontAwesomeIcon icon={faAngleRight} /></Link>
            </div>
          </div>
          <div className="menu-category">
            <h3 className="menu-category-name">Инвентарь</h3>
            <div className="menu-category-items">
              <Link to="/inventory/Dumbbells" className="menu-category-item">Гантели <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/EzStange" className="menu-category-item">EZ-гриф <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Stange" className="menu-category-item">Штанга <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Trx" className="menu-category-item">TRX-петли <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Smit" className="menu-category-item">Трненажер Смита <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item">Скамья Скотта <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item">Фитнес-бол <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item">Скакалка <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/ownweight" className="menu-category-item">Собственный вес <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item">Степы <FontAwesomeIcon icon={faAngleRight} /></Link>
            </div>
          </div>
          <div className="menu-category">
            <h3 className="menu-category-name">Тип упражнения</h3>
            <div className="menu-category-items">
              <Link to="/type/Base" className="menu-category-item">Базовое <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/type/Isolated" className="menu-category-item">Изолирующее <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/stats/efficiency" className="menu-category-item">Самые эффективные  <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/stats/injury" className="menu-category-item">Самые безопасные  <FontAwesomeIcon icon={faAngleRight} /></Link>
            </div>
          </div>
        </Menu>
    );
  }
}

export default SideMenu;



